.friend-list {
    list-style: none;
    display: block;
    width: 40%;
    background-color: #dff0d8;
    border-radius: 6px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    margin: 40px auto;
    padding: 0;
  }
  
  .friend-item {
    display: table-cell;
    vertical-align: top;
    text-align: center;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 8px;
  }
  
  .avatar {
    display: block;
    margin: 0 auto;
    width: 10%;
  }
  
  .name {
    display: block;
    align-items: normal;
    margin: 0 auto;
    background-color: #fcf8e3;
  }
  
  .status {
    display: block;
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background-color: transparent;
    border: 1px solid #ccc;
    margin: 10px auto 0;
  }
  
  .offline {
    background-color: #FF5733; 
  }